@import url('https://fonts.googleapis.com/css?family=Baloo+Thambi+2&display=swap');
* {
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  color: rgba(0, 0, 0, 0.9);
  font-family: 'Baloo Thambi 2', sans-serif;
  font-size: 13px;
}

h1 {
  display: flex;
  align-items: flex-end;
  line-height: 18px;
  font-size: 18px;
}

.header-icon {
  margin-right: 5px;
}

.main-content {
  flex: 1 0;
  flex-basis: 1024px;
  padding: 20px;
}

.search-input {
  width: 800px;
  margin-top: 20px;
}

.search-input input {
  border: 0;
  border-radius: 5px;
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.15);
  background: white;
  padding: 10px 20px;
  width: 100%;
  height: 100%;
}

.search-input input:focus {
  outline: none;
  box-shadow: 0 0 8px 1px rgba(33, 150, 243, 0.4);
}

.repo-list {
  margin-top: 30px;
  border-radius: 5px;
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.15);
  width: 100%;
}

.repo-list ul {
  padding: 0;
  list-style: none;
}

.repo-list li {
  display: flex;
  padding: 20px;
  width: 100%;
  list-style: none;
}

.list-item {
  transition: all 0.5s ease;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.list-item:hover {
  box-shadow: inset 0 -2px 2px 1px rgba(0, 0, 0, 0.1);
}

.list-item-active {
  border-bottom: none;
}

.list-cell {
  display: flex;
  align-items: center;
  padding: 0 10px;
  width: 80px;
}

.list-cell.pulse {
  width: 130px;
}

.list-cell-value {
  margin-left: 5px;
}

.repo-name {
  flex: 1 0;
}

.commit-item {
  display: flex;
  padding: 10px 30px;
  width: 100%;
}

.commit-message {
  max-width: 450px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.commit-item div {
  padding-right: 3px;
}

.loading-message {
  padding: 10px 30px 20px;
}
